<template>
  <div class="emoji" v-show="showface">
    <!-- 表情区域 -->
    <ul>
      <li v-for="(item,index) in faceList" :key="index" @click="getBrow(index)">{{item}}</li>
    </ul>
  </div>
</template>

<script>
// 导入JSON格式的表情库
const appData = require("@/assets/data/emojis.json");
export default {
  name: "emoji",
  data() {
    return {
      faceList: [],
      showface: false
    };
  },
  created() {
    this.faceContent();
    this.$Buse.$on("hiddenemoji", () => {
      this.showface = false;
    });
    this.$Buse.$on("toggleemoji", () => {
      this.showface = !this.showface;
    });
  },
  methods: {
    // 初始化表情
    faceContent() {
      for (let i in appData) {
        this.faceList.push(appData[i].char);
      }
    },
    // 获取用户点击之后的标签 ，存放到输入框内
    getBrow(index) {
      this.$Buse.$emit("returnemoji", this.faceList[index]);
    }
  }
};
</script>

<style scoped lang="scss">
@import "@/assets/scss/index.scss";
.emoji {
  position: absolute;
  top: -54px;
  left: 0;
  padding-top: 3px;
  padding-left: 10px;
  font-size: 18px;
  @include fontnor_18;
  width: 98%;
  height: 50px;
  overflow-y: scroll;
  @include background($background-color-theme);
  & ul li {
    width: 26px;
    height: 26px;
    line-height: 25px;
    float: left;
    &:hover {
      cursor: pointer;
      @include fontnor_20;
    }
  }
}

</style>
