<template>
  <div class="chatroom">
    <div class="chat">
      <div class="userList">
        <div class="list">
          <span class="item" @click="setpublic()">广场({{onlineuserslist.length}})</span>
          <el-collapse-transition>
            <div class="pubuserlist" v-show="true">
              <li
                v-for="(item,index) in onlineuserslist"
                :key="index"
                @click="prevaite(item.userId,item.username)"
              >
                <div v-if="item.userId !== $store.getters.getcurrentUser.userId">
                  <el-badge :value="item.number" :max="10">
                    <span class="useravatar">
                      <img
                        width="30"
                        height="30"
                        style="border-radius:50%;"
                        :src="item.avatar"
                        :alt="item.username"
                      />
                    </span>
                  </el-badge>
                  <span class="username">{{item.username}}</span>
                </div>
              </li>
            </div>
          </el-collapse-transition>
        </div>
      </div>
      <div class="dialog">
        <span class="title">{{dialog}}</span>
        <div class="content" ref="content">
          <ul>
            <li
              v-for="(item,index) in (destination==='all'?publicdialog:prevaitedialog)"
              :key="index"
            >
              <div :class="item.userId===$store.getters.getcurrentUser.userId?'right':'left'">
                <div class="basemsg">
                  <img :src="item.avatar" width="30" height="30" style="border-radius:50%;" alt />
                  <span class="mininame">{{item.username | format2(item.username)}}</span>
                </div>
                <div class="word">{{item.content}}</div>
              </div>
            </li>
          </ul>
        </div>
        <div class="tap">
          <emoji />
          <nav class="control">
            <span class="el-icon-info icon" @click="toggleemoji"></span>
            <span class="el-icon-picture-outline icon"></span>
            <span class="el-icon-folder-opened icon"></span>
            <button class="biu" @click="zouni">发送(ctrl+enter)</button>
          </nav>
          <textarea
            name="message"
            class="myinput"
            rows="10"
            maxlength="150"
            v-model="message"
            @keyup.ctrl.enter.prevent="zouni"
            @focus="$Buse.$emit('hiddenemoji')"
          ></textarea>
        </div>
      </div>
    </div>
  </div>
</template>

<script scoped>
import { getPublicDialog, getPrivateDialog } from "@/api/index";
import emoji from "@/components/emoji.vue";
import io from "socket.io-client/dist/socket.io";
export default {
  data() {
    return {
      dialog: "",
      message: "",
      destination: "all",
      onlineuserslist: [],
      prevaitedialog: [],
      publicdialog: []
    };
  },
  components: { emoji },
  mounted() {
    console.log(this.onlineuserslist)
    //---------------socket部分------------------
    var socket = io("http://qctx.work");
    socket.on("connect", () => {
      let userId = this.$store.getters.getcurrentUser.userId;
      if (!userId) {
        return this.$message("请先登陆!");
      }
      socket.emit("userbase", userId);
    });
    socket.on("message", data => {
      if (this.destination === "all") {
        this.publicdialog.push(data);
      }
    });
    socket.on("privatemessage", data => {
      if (
        this.destination === data.userId ||
        this.$store.getters.getcurrentUser.userId === data.userId
      ) {
        this.prevaitedialog.push(data);
      } else {
        if (data.userId !== this.$store.getters.getcurrentUser.userId) {
          this.$notify({
            iconClass: "el-icon-message-solid",
            title: "未读消息！",
            message: data.username + "给您发来消息!",
            position: "bottom-right"
          });
        }
      }
    });
    socket.on("online_users", userlist => {
      this.onlineuserslist = userlist;
    });
    //-------------------socket结束---------------------
    //监听登出时间
    this.$Buse.$on("logout", () => {
      socket.emit("logout");
    });
    //监听添加表情
    this.$Buse.$on("returnemoji", emoji => {
      this.message += emoji;
    });
    //监听公共聊天发送
    this.$Buse.$on("putpublic", () => {
      let { username, userId, avatar } = this.$store.getters.getcurrentUser;
      socket.emit("publicdialog", {
        content: this.message,
        userId,
        username,
        avatar
      });
    });
    //监听私聊
    this.$Buse.$on("putprivate", () => {
      let { username, userId, avatar } = this.$store.getters.getcurrentUser;
      socket.emit("privatedialog", {
        content: this.message,
        touserId: this.destination,
        userId,
        username,
        avatar
      });
    });
    //init
    this.setpublic();
  },
  methods: {
    zouni() {
      if (this.destination === "all") {
        this.$Buse.$emit("putpublic");
      } else if (this.destination === "group") {
        console.log("这是组发的");
      } else {
        this.$Buse.$emit("putprivate");
      }
      this.message = "";
    },
    setdialogTitle(title) {
      this.dialog = title;
    },
    setpublic() {
      this.setdialogTitle("广场");
      this.destination = "all";
      getPublicDialog().then(res => {
        this.publicdialog = res.data.dialog;
      });
    },
    prevaite(userId, username) {
      this.destination = userId;
      this.setdialogTitle(username);
      let params = {
        userId,
        touserId: this.$store.getters.getcurrentUser.userId
      };
      getPrivateDialog(params).then(res => {
        this.prevaitedialog = res.data.dialog;
      });
    },
    toggleemoji() {
      this.$Buse.$emit("toggleemoji");
    },
    togglePub() {
      this.usershow = !this.usershow;
    }
  },
  beforeDestroy() {
    this.$Buse.$off("putpublic");
    this.$Buse.$off("putprivate");
    this.$Buse.$off("toggleemoji");
  },
  filters: {
    formatname: data => {
      return data.length > 6 ? data.slice(0, 5) + ".." : data;
    },
    format2: data => {
      if(!data) return null
      return data.length > 2 ? data.slice(0, 2) + ".." : data;
    }
  },
  watch: {
    publicdialog: function() {
      this.$nextTick(() => {
        let content = document.querySelector(".content");
        content.scrollTop = content.scrollHeight;
      });
    },
    prevaitedialog: function() {
      this.$nextTick(() => {
        let content = document.querySelector(".content");
        content.scrollTop = content.scrollHeight;
      });
    }
  }
};
</script>

<style scoped lang="scss">
@import "@/assets/scss/index.scss";
.chatroom {
  max-width: 1200px;
  margin: 0 5%;
  width: 90%;
  height: 100%;
  box-sizing: border-box;
  overflow: hidden;
  @include fontnor_16;
  & .chat {
    position: relative;
    margin-top: 2.5%;
    height: 95%;
    @include background($background-color-theme, 0.9);
    box-sizing: border-box;
    display: flex;
    & .userList {
      flex-basis: 20%;
      border-right: 1px #ccc solid;
      overflow-y: scroll;
      & .list {
        & .item {
          @include fontnor_16;
          display: block;
          padding: 10px 0;
          text-align: center;
          cursor: pointer;
        }
        & .pubuserlist li {
          position: relative;
          width: 100%;
          font-size: 20px;
          padding-top: 2px;
          text-align: center;
          & img {
            display: block;
            width: 30px;
            margin: 0 auto;
          }
          & .username {
            min-width: 30px;
            width: 100%;
            height: 30px;
            line-height: 30px;
            padding-left: 5px;
            vertical-align: middle;
            overflow: hidden;
            text-overflow: ellipsis;
            text-align: center;
          }
        }
      }
    }
    & .dialog {
      flex: 1;
      & .title {
        text-align: center;
        display: inline-block;
        letter-spacing: 8px;
        width: 100%;
        height: 8%;
        @include font1_24;
        line-height: 50px;
        letter-spacing: 0px;
        font-weight: 1000;
        border-bottom: 1px solid #ccc;
      }
      & .content {
        width: 100%;
        height: 75%;
        padding-top: 3px;
        padding-bottom: 24px;
        box-sizing: border-box;
        position: relative;
        overflow-y: scroll;
        & li {
          margin-top: 10px;
        }
        & .basemsg {
          display: inline-block;
          width: 50px;
          @include font1_20;
          @include color($font-color-shallow9);
          text-align: center;
          .mininame {
            display: block;
            max-width: 50px;
            text-align: center;
            white-space: nowrap;
          }
        }
        & .word {
          display: inline-block;
          text-align: left;
          vertical-align: top;
          min-width: 30px;
          max-width: 200px;
          min-height: 25px;
          padding: 3px 10px;
          line-height: 25px;
          border-radius: 5px;
          @include fontnor_14;
          @include background($font-color-shallowdb);
          white-space: pre-line;
        }
        & .right {
          text-align: right;
          min-height: 58px;
          & .basemsg {
            float: right;
          }
        }
      }
      & .tap {
        height: 20%;
        width: 80%;
        position: absolute;
        right: 0;
        bottom: 0;
        border-top: 1px #ccc solid;
        & .control {
          height: 25%;
          padding: 5px 5px 0 10px;
          box-sizing: border-box;
          @include background($background-color-theme, 0.8);
          & .icon {
            @include fontnor_20;
            vertical-align: top;
            padding-right: 10px;
            cursor: pointer;
          }
          & .biu {
            padding: 3px;
            font-family: "yushou";
            font-size: 20px;
            @include font1_20;
            border: none;
            float: right;
            outline: none;
            background-color: transparent;
            @media screen and (min-width: 600px) {
              &:hover {
                @include color($background-color-theme);
                @include background($font-color-theme);
              }
            }
          }
        }
      }
    }
  }
}

.myinput {
  position: absolute;
  padding: 10px;
  line-height: 1;
  width: 100%;
  height: 75%;
  bottom: 0;
  box-sizing: border-box;
  border: none;
  font-family: "yushou";
  font-size: 20px;
  outline: none;
  @include background($background-color-theme, 0.8);
  resize: none;
}
</style>
